@import url('https://fonts.googleapis.com/css?family=Reboto&display=swap');
*{
  box-sizing:border-box;
}
body{
  margin :0;
  background-color:green;
  font-family:Reboto, sans-serif;
  min-height:100vh;
  display:flex;
  align-items:center;
  justify-content:center;
} 
.calculator{
  background-color:#111;
  box-shadow:2px 2px 5px rgba(0,0,0,0.5);
  display:flex;
  flex-wrap:wrap;
  padding:5px;
  width:250px;
  height:390px;
}
.display{
  color:#fff;
  text-align:right;
  position:relative;
  font-size:36px;
  padding:20px 10px;
  width:100%;
  height:70px;
}
.display small{
  color:#EE9406;
  position: absolute;
  top:5px;
  right:5px;
  font-size:12px;
}
.nums-container{
  width:75%;
}
.ops-container{
  width:25%;
}
button{
  border:none;
  border-radius:50px;
  font-size:24px;
  cursor:pointer;
  margin:5px;
  height:50px;
  width:50px;
}
button.big-v{
  border-radius:50px;
  height:110px;
}
button.big-h{
  border-radius:50px;
  width:110px;
}
button.orange{
  background-color:#EE9406;
  color:#fff;
}
button.dark-grey{
  background-color:#323232;
  color:#fff;
}
button.light-grey{
  background-color:#A5A5A5;
  color:#fff;
}
button.ac{
  margin-right:50%;
}
button:active{
  transform: scale(0.9);
}
button:focus{
  outline: none;
}
button:hover{
  opacity:0.98;
}